    const { totalCount, pageInfo: { hasNextPage, endCursor } } = deepGet(data, paginator.path);
    const { nodes } = deepGet(currentData, paginator.path);
    paginator.remainingRequests = getRemainingNumberOfRequests(paginator, currentData, maxPerPage);
